@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');

* {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #202632;
}

.container {
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 10px solid #101116;
    background: #101116;
    border-radius: 5px;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px,auto);
}

input[type="text"] {
    height: 75px;
    width: 249px;
    background-color: #101116;
    border: 0px;
    color: #ffffff;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;
}

#clear {
    grid-column: 1/3;
    grid-row: 1;
    color: #52ffa8;
}

#result {
    grid-column: 3/5;
    grid-row: 5;
    color: #52ffa8;
}

#backspace {
    color: #52ffa8; 
}

button {
    margin: 4px;
    
    cursor: pointer;
    background: transparent; 
    color: #ffffff;
    font-weight: 500;
    border: 1px solid #52ffa8;
    border-radius: 5px;
}

button:hover,
.highlight:hover,
#backspace:hover,
#result:hover,
#clear:hover {
    color: #202632;
    background: #52ffa8;
    transition: 0.2s; 
    box-shadow: 0px 0px 25px 2px #52ffa8;
    
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

.highlight {
    background: #687180;
}
